import { flexbox, typography } from "@mui/system";
import React from "react";
import './style.css'
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Grid from '@mui/material/Grid';
import DatePicker from 'react-date-picker';

const TodayExpense = ({selectedDateExpense = {}})=>{
      const [value, onChange] = React.useState(new Date());

    return (
        // <div style={{display:'flex', flexDirection:"column", height:'100%'}}>
        <Grid container direction="column">
            <Grid item>
            {`Add to date: `}
            <DatePicker
                    onChange={onChange}
                    value={value}
                    maxDate = {new Date()}
                    clearIcon = {null}
                    format = 'dd-MM-y'
                />
            </Grid>
            <Grid container spacing={1} alignItems="center">
                <Grid item xl ={4} md={4} xs={12} className='padding0'>
                    <h3>{ value && (value.toDateString())} Expense</h3>
                </Grid>
                <Grid item xl ={4} md={4} xs={12}>
                <FormControl fullWidth size="small">
                        <InputLabel id="demo-simple-select-label">Category</InputLabel>
                        <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={''}
                            label="category"
                            onChange={()=>{}}
                        >
                            <MenuItem value={"Food"}>Food</MenuItem>
                            <MenuItem value={"Transport"}>Transport</MenuItem>
                            <MenuItem value={"Entertainment"}>Entertainment</MenuItem>
                            <MenuItem value={"Utility"}>Utility</MenuItem>
                        </Select>
                    </FormControl>
                </Grid>
                <Grid item xl ={4} md={4} xs={12}>
                </Grid>
            </Grid>
            <div>
                <ul className='todayExpenseLi'>
                    {
                        selectedDateExpense && Object.keys(selectedDateExpense).length>0 && 
                        Object.keys(selectedDateExpense).map(item=>{
                        return <li className='dayExpLi' key=''>{`${item} ${selectedDateExpense[item][1]} ${selectedDateExpense[item][0]}₹`}<button>Delete</button> </li>
                    })
                    }
                </ul>
            </div>
            <div>
                Total Cost
            </div>
        </Grid>
        // </div>
    )
}


export default TodayExpense;