import React, {useState} from "react";
import AddExpenseBar from "../AddExpenseBar/AddExpenseBar";
import './style.css'
import ColumnChart from "../ColumnChart/ColumnChart";
import TodayExpense from "../TodayExpense/TodayExpense"

const Section = ()=>{

    const [selectedDateExpense, setSelectedDateExpense] = useState({})

    const confirmedItems = (obj)=>{
        setSelectedDateExpense({...selectedDateExpense, ...obj})
    }

    return (
        // <div>
            <div className = 'section'>
                <div className='height100'>
                <AddExpenseBar confirmedItems={confirmedItems}/>
                <TodayExpense selectedDateExpense={selectedDateExpense}/>
                </div>
                <ColumnChart/>
            </div>
        // </div>
    )
}


export default Section;